import React from 'react';

import SectionTitle from '../components/SectionTitle';
import Section from '../components/Section';

function Awards(props) {
  const classes = props.classes;

  return (
    <div style={{width: '100%', marginBottom: 50}}>
      <SectionTitle title="Awards" icon="star_border" />
      <Section
        title="Project Appreciation"
        subTitle="Voice of Citizen 2016"
        heading="HEWLETT PACKARD ENTERPRISE, SINGAPORE"
        list={[
          "Received recognition for the outstanding work during World City Summit and Phuket smart city held in year 2016."
        ]}
      />
      <Section
        title="Best project in Implementation"
        subTitle="ZETA 2014"
        heading="Tata Consultancy Services, India"
        list={[
          "Won the award for the best project in the year 2014.",
          "This is annually held competition within the different branch offices of Tata Consultancy Service."
        ]}
      />
      <Section
        title="Young Innovation Award"
        subTitle="Patent Number IN-821064, 2011-2012"
        heading="Dynamic Desktop and Aggregator"
        list={[
          "This is an application written in c# which dynamically aggregates content from the current system and web(rss/facebook/gmail) and creates desktop wallpaer from it.",
          "This has a very minimal footprint in terms of resources on the system.",
          "This also had a web interface where users of the application can upload/download themes created by the community."
        ]}
      />
      <Section
        title="AICTE Scholarship"
        subTitle="2008 - 2012"
        heading="All India Council for Technical Education"
        list={[
          "Received scholarship from Indian government council for technical education for engineering."
        ]}
      />
    </div>
  );
}

export default Awards;



