import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { withStyles } from 'material-ui/styles';
import { cyan } from 'material-ui/colors';

import Typography from 'material-ui/Typography';
import Icon from 'material-ui/Icon';

const styles = theme => ({
  avatar: {
    margin: 10,
    marginLeft: 0,
    color: '#fff',
    backgroundColor: cyan[500],
  },
  row: {
    display: 'flex',
    alignItems: 'center',
    marginTop: 10
  },
  icon: {
    marginRight: 10,
    color: cyan[500],
  },
  container: {
    marginTop: 30
  }
});

function Contact(props) {
  const classes = props.classes;

  return (
    <div className={classes.container}>
      <div className={classes.row}>
        <Icon className={classes.icon}>email</Icon>
        <Typography type="body1" className={classNames(classes.textCenter, classes.blue)}>
          <a href="mailTo:textmav@gmail.com">textmav@gmail.com</a>
        </Typography>
      </div>
      <div className={classes.row}>
        <Icon className={classes.icon}>phone</Icon>
        <Typography type="body1" className={classNames(classes.textCenter, classes.blue)}>
          <a href="callTo:+852-69202252">+852-69202252</a>
        </Typography>
      </div>
      <div className={classes.row}>
        <Icon className={classes.icon}>phone</Icon>
        <Typography type="body1" className={classNames(classes.textCenter, classes.blue)}>
          <a href="callTo:+65-87267271">+65-87267271</a>
        </Typography>
      </div>
      <div className={classes.row}>
        <Icon className={classes.icon}>language</Icon>
        <Typography type="body1" className={classNames(classes.textCenter, classes.blue)}>
          <a target="_blank" href="https://linkedin.com/in/naveen13">https://linkedin.com/in/naveen13</a>
        </Typography>
      </div>
    </div>
  );
}

Contact.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Contact);



