import React from 'react';
import PropTypes from 'prop-types';
import Typography from 'material-ui/Typography';
import Button from 'material-ui/Button';
import { withStyles } from 'material-ui/styles';
import { cyan, blueGrey } from 'material-ui/colors';

import ListSubheader from 'material-ui/List/ListSubheader';
import List, { ListItem, ListItemIcon, ListItemText } from 'material-ui/List';

import SectionTitle from '../components/SectionTitle';
import Section from '../components/Section';

const styles = theme => ({
  row: {
    display: 'flex',
    alignItems: 'center',
    marginTop: 20
  },
  link: {
    textTransform: 'lowercase',
    color: blueGrey[600]
  },
  container: {
    width: '100%'
  },
  blue: {
    color: cyan[600],
  },
  textTitle: {
    lineHeight: 1.2,
  },
  black: {
    color: blueGrey[600]
  },
  title: {
    marginBottom: 20,
    paddingRight: 20,
  },
  heading: {
    marginBottom: 10,
  },
});

function Experiences(props) {
  const classes = props.classes;

  return (
    <div style={{width: '100%', marginBottom: 50}}>
      <SectionTitle title="Experiences" icon="work" />
      <Section
        className={classes.section}
        title="Senior Software Engineer, Specialist"
        subTitle="April 2014 - Present"
        heading="HEWLETT PACKARD ENTERPRISE, SINGAPORE"
        list={[
          "Work with a group of developers and data scientists to create data-analytics applications and dashboards.",
          "Some of these dashboards can be accessed at.",
          <div>
          <Button dense className={classes.link} target="_blank" href="https://hpe-innovation.center">https://hpe-innovation.center</Button>
          <Button dense className={classes.link} target="_blank" href="http://hpe-innovation.center/its">http://hpe-innovation.center/its</Button>
          <Button dense className={classes.link} target="_blank" href="http://hpe-innovation.center/nafta">http://hpe-innovation.center/nafta</Button>
          </div>,
          "Present solution developed to customers and media as and when needed.",
          "Developed a cross platform mobile application solution targetted for Enterprises.",
          "Create a developer console for for one of the HPE offerings and conduct trainings for it."
        ]}
      />
      <Section
        className={classes.section}
        title="ASSISTANT SOFTWARE ENGINEER"
        subTitle="September 2012 - March 2014"
        heading="TATA CONSULTANCY SERVICES, INDIA"
        list={[
          "Responsible for designing user interfaces for web and mobile applications",
          "Developement of hybrid mobile applications and their supporting websites",
        ]}
      />
      <div className="pagebreak"></div>
      <Section
        className={classes.section}
        title="Co-Founder"
        subTitle="October 2011 - August 2014"
        heading="CRONJ, INDIA"
        list={[
          "I co-founded this company with two college friends.",
          "We primarily used to work in Javascript / NodeJS",
          "In short period of time, we collaborated with some big companies in USA and Europe to develop a variety of hybrid mobile applications, HTML5 canvas based games and commercial websites."
        ]}
      />
      <Section
        className={classes.section}
        title="Trainee"
        subTitle="June 2011 - July 2011"
        heading="COMPRO INFORMATION TECHNOLOGIES CORPORATION, ISTANBUL TURKEY"
        list={[
          "Provided solution for efficient backup mechanism for the IBM Tivoli Storage Manager with Server side scripting using windows PowerShell and Bash.",
        ]}
      />
    </div>
  );
}

Experiences.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Experiences);



