import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { withStyles } from 'material-ui/styles';
import { cyan, blueGrey } from 'material-ui/colors';
import Typography from 'material-ui/Typography';
import Grid from 'material-ui/Grid';
import ListSubheader from 'material-ui/List/ListSubheader';
import List, { ListItem, ListItemIcon, ListItemText } from 'material-ui/List';
import Divider from 'material-ui/Divider';

const styles = theme => ({
  row: {
    display: 'flex',
    alignItems: 'center',
    marginTop: 20,
    marginBottom: 40,
  },
  container: {
    width: '100%'
  },
  blue: {
    color: cyan[600],
  },
  textTitle: {
    lineHeight: 1.2,
    textTransform: 'uppercase',
  },
  black: {
    color: blueGrey[600]
  },
  title: {
    marginBottom: 20,
    paddingRight: 20,
  },
  heading: {
    marginBottom: 10,
  },
});

function Section(props) {
  const { classes, heading, subHeading, title, subTitle, list } = props;

  function getContent(){
    if(list && list.length) {
      return (<List className="lists">{list.map((i,j) => <ListItem key={j} dense={true}>
        <ListItemText primary={<Typography type="body1" className={classNames(classes.textCenter, classes.black)}>{i}</Typography>} />
      </ListItem>)}</List>);
    }
    return '';
  }
  return (
    <div className={classes.row}>
      <Grid container>
        <Grid item xs={12} sm={12} md={4} className={classes.title}>
          <Typography type="title" className={classNames(classes.textTitle, classes.textCenter, classes.blue)}>{title}</Typography>
          <Typography type="caption" className={classNames(classes.textCenter, classes.blue)}>{subTitle}</Typography>
        </Grid>
        <Grid item xs={12} sm={12} md={8}>
          <Typography type="headline" className={classNames(classes.textCenter, classes.black, classes.heading)}>{heading}</Typography>
          {getContent()}
        </Grid>
      </Grid>
    </div>
  );
}

Section.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Section);



