import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { withStyles, createStyleSheet } from 'material-ui/styles';
import Avatar from 'material-ui/Avatar';
import naveenImage from '../../src/assets/images/pic.jpg';
import { cyan } from 'material-ui/colors';
import Typography from 'material-ui/Typography';

const styles = theme => ({
  row: {
    display: 'flex',
    justifyContent: 'center',
    flexDirection: 'column',
    alignItems: 'center',
    width: '100%'
  },
  avatar: {
    margin: 10,
  },
  bigAvatar: {
    width: 200,
    height: 200,
  },
  textCenter: {
    textAlign: 'center',
  },
  title: {
    color: cyan[800]
  },
  subtitle: {
    color: cyan[800]
  }
});

function Sidebar(props) {
  const classes = props.classes;

  return (
    <div className={classes.row}>
      <Avatar
        alt="Naveen Kumar"
        src={naveenImage}
        className={classNames(classes.avatar, classes.bigAvatar)}
      />
      <Typography type="display1" gutterBottom className={classNames(classes.textCenter, classes.title)}>
        Naveen Kumar
      </Typography>
      <Typography type="subheading" gutterBottom className={classNames(classes.textCenter, classes.subtitle)}>
        Senior Software Engineer, Specialist
      </Typography>
      {props.content}
    </div>
  );
}

Sidebar.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Sidebar);



