import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { withStyles, createStyleSheet } from 'material-ui/styles';
import { cyan } from 'material-ui/colors';

import Typography from 'material-ui/Typography';

const styles = theme => ({
  row: {
    display: 'flex',
    alignItems: 'center'
  },
  container: {
    marginTop: 40,
  },
  body: {
    color: 'rgba(0,0,0,0.6)',
    marginTop: 10,
    textAlign: 'justify'
  },
  blue: {
    color: cyan[600]
  }
});

function Objective(props) {
  const classes = props.classes;

  return (
    <div className={classes.container}>
      <div className={classes.row}>
        <Typography type="headline" className={classNames(classes.textCenter, classes.blue)}>
          Objective
        </Typography>
      </div>
      <div className={classes.row}>
        <Typography type="body1" className={classes.body}>
          Highly motivated Software Engineer with +5 years of experience in Web and Mobile Application development. Seeking to leverage my technical and professional expertise to achieve new levels of competence.
        </Typography>
      </div>
    </div>
  );
}

Objective.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Objective);



