import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { withStyles } from 'material-ui/styles';
import { cyan } from 'material-ui/colors';
import Icon from 'material-ui/Icon';
import Typography from 'material-ui/Typography';

const styles = theme => ({
  row: {
    display: 'flex',
    alignItems: 'center',
    marginTop: 20
  },
  blue: {
    color: cyan[600]
  },
  icon: {
    marginRight: 10,
    backgroundColor: cyan[500],
    color: 'white',
    fontSize: 30,
    borderRadius: '50%',
    padding: 10,
  },
  filler: {
    background: 'url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAFCAYAAACNbyblAAAAG0lEQVQYV2NkwAIY8QuuOfifIcQerIiASiRzAJtUAwbC0PglAAAAAElFTkSuQmCC) repeat',
    height: '20px',
    width: '100%',
    margin: '0 10px'
  }
});

function SectionTitle(props) {
  const { classes, icon, title } = props;
  return (
    <div className={classes.row}>
      <Icon className={classes.icon}>{icon}</Icon>
      <Typography type="display1" className={classNames(classes.textCenter, classes.blue)}>{title}</Typography>
      <div className={classes.filler} />
    </div>
  );
}

SectionTitle.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(SectionTitle);



