import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { withStyles } from 'material-ui/styles';
import Avatar from 'material-ui/Avatar';
import { cyan } from 'material-ui/colors';
import Grid from 'material-ui/Grid';
import Typography from 'material-ui/Typography';
import Icon from 'material-ui/Icon';

const styles = theme => ({
  row: {
    display: 'flex',
    alignItems: 'center',
  },
  container: {
    marginTop: 40,
  },
  body: {
    color: 'rgba(0,0,0,0.6)',
    marginTop: 10,
    textAlign: 'justify',
    display: 'flex',
    alignItems: 'center',
  },
  blue: {
    color: cyan[600]
  }
});

function Skills(props) {
  const classes = props.classes;

  function getRating(title, count){
    let stars = [];
    for(let i=0; i<count && stars.push(i); i++);
    return (<Grid container><Grid item xs>
      {title}
    </Grid>
    <Grid item xs className='star'>
      {stars.map((i,j) => <Icon key={j} color="accent">grade</Icon>)}
    </Grid></Grid>)
  }

  return (
    <div className={classes.container}>
      <div className={classes.row}>
        <Typography type="headline" className={classNames(classes.textCenter, classes.blue)}>
          Professional Skills
        </Typography>
      </div>
      <div className={classes.body}>{getRating('HTML', 5)}</div>
      <div className={classes.body}>{getRating('SCSS / CSS', 5)}</div>
      <div className={classes.body}>{getRating('Javascript', 5)}</div>
      <div className={classes.body}>{getRating('NodeJS', 5)}</div>
      <div className={classes.body}>{getRating('Photoshop', 5)}</div>
      <div className={classes.body}>{getRating('Illustator', 5)}</div>
      <div className={classes.body}>{getRating('AngularJS', 5)}</div>
      <div className={classes.body}>{getRating('ReactJS', 5)}</div>
      <div className={classes.body}>{getRating('D3JS', 5)}</div>
      <div className={classes.body}>{getRating('Java', 3)}</div>
      <div className={classes.body}>{getRating('C#', 2)}</div>
      <div className={classes.body}>{getRating('Swift', 1)}</div>
    </div>
  );
}

Skills.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Skills);



